            <el-cascader
              v-if="searchchoicenum == 1"
              v-model="searchchoice"
              placeholder="请选择具体类别"
              :options="options1"
              :props="{ expandTrigger: 'hover' }"
              style="width: 200px"
              :value="value"
              @change="searchitem(searchchoice)"
            ></el-cascader>

            <el-cascader
              v-if="searchchoicenum == 2"
              v-model="searchchoice"
              placeholder="请选择具体类别"
              :options="options2"
              :props="{ expandTrigger: 'hover' }"
              style="width: 200px"
              :value="value"
              @change="searchitem(searchchoice)"
            ></el-cascader>

            <el-cascader
              v-if="searchchoicenum == 3"
              v-model="searchchoice"
              placeholder="请选择具体类别"
              :options="options3"
              :props="{ expandTrigger: 'hover' }"
              style="width: 200px"
              :value="value"
              @change="searchitem(searchchoice)"
            ></el-cascader>

            <el-cascader
              v-if="searchchoicenum == 4"
              v-model="searchchoice"
              placeholder="请选择具体类别"
              :options="options4"
              :props="{ expandTrigger: 'hover' }"
              style="width: 200px"
              :value="value"
              @change="searchitem(searchchoice)"
            ></el-cascader>

            <el-cascader
              v-if="searchchoicenum == 5"
              v-model="searchchoice"
              placeholder="请选择具体类别"
              :options="options5"
              :props="{ expandTrigger: 'hover' }"
              style="width: 200px"
              :value="value"
              @change="searchitem(searchchoice)"
            ></el-cascader>
          </el-menu-item>

          <el-menu-item
            style="cursor: default; position: absolute; right: 150px"
          >
            <el-input
              placeholder="请输入内容"
              prefix-icon="el-icon-search"
              v-model="keywords"
              style="width: 200px"
              @keyup.enter.native="search(keywords)"
            >
            </el-input>
            <el-button style="margin-top: 3px" @click="search(keywords)"
              >搜索</el-button
            >
          </el-menu-item>

          <el-menu-item style="cursor: default; position: absolute; right: 0">
            <el-button style="margin-top: 3px">前往资源页</el-button>
          </el-menu-item>
        </el-menu>
      </div>
    </el-row>

    <el-row type="flex" justify="center" style="margin-top: 25px">
      <el-col :span="0.5" style="margin-right: 25px">
        <el-row>
          <HomeNewsListVue />
        </el-row>
        <el-row>
          <HomeAuthorListVue />
        </el-row>
        <el-row>
          <HomeFoucsListVue />
        </el-row>
      </el-col>
      <el-col :span="0.5" v-if="article_list.length != 0">
        <ul name="HomeArticleList" class="HomeArticleList-warp">
          <li>
            <article_home_itemVue
              style="margin-top: 20px"
              v-for="(_, index) in article_list"
              :articleList="article_list[index]"
              :key="index"
            />
          </li>
        </ul>
      </el-col>
      <el-col :span="0.5" v-if="article_list.length == 0">
        <div class="noList">
          <font>暂未搜索到该分类的文章哦......</font>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getArticleListAPI } from "@/api/homeAPI/articleslist";
import HomeArticleListVue from "@/components/ModelHome/HomeArticleList.vue";
import HomeNewsListVue from "@/components/ModelHome/HomeNewsList.vue";
import HomeAuthorListVue from "@/components/ModelHome/HomeAuthorList.vue";
import HomeFoucsListVue from "@/components/ModelHome/HomeFoucsList.vue";
import article_home_itemVue from "../../components/little/article_home_item.vue";
export default {
  name: "index-home",
  data() {
    return {
      keywords: "",
      value: [],
      activeIndex: "1",
      searchchoicenum: 0,
      searchchoice: [],
      messageList: [],
      article_list: [],
      article_recommond: [],
      article_list_all: [],
      options1: [
        {
          value: "中国语言文学类",
          label: "中国语言文学类",
        },
        {
          value: "新闻传播学类",
          label: "新闻传播学类",
        },
        {
          value: "外国语言文学类",
          label: "外国语言文学类",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options2: [
        {
          value: "信息与计算科学",
          label: "信息与计算科学",
        },
        {
          value: "数学与应用数学",
          label: "数学与应用数学",
        },
        {
          value: "应用化学",
          label: "应用化学",
        },
        {
          value: "生物技术",
          label: "生物技术",
        },
        {
          value: "生物科学",
          label: "生物科学",
        },
        {
          value: "应用物理学",
          label: "应用物理学",
        },
        {
          value: "应用心理学",
          label: "应用心理学",
        },
        {
          value: "统计学",
          label: "统计学",
        },
        {
          value: "化学",
          label: "化学",
        },
        {
          value: "物理学",
          label: "物理学",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options3: [
        {
          value: "土木工程",
          label: "土木工程",
        },
        {
          value: "机械类专业",
          label: "机械类专业",
        },
        {
          value: "电气工程及其智能化",
          label: "电气工程及其智能化",
        },
        {
          value: "计算机科学与技术",
          label: "计算机科学与技术",
        },
        {
          value: "车辆工程",
          label: "车辆工程",
        },
        {
          value: "软件工程",
          label: "软件工程",
        },
        {
          value: "交通运输工程",
          label: "交通运输工程",
        },
        {
          value: "自动化",
          label: "自动化",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options4: [
        {
          value: "音乐",
          label: "音乐",
        },
        {
          value: "舞蹈",
          label: "舞蹈",
        },
        {
          value: "影视学类",
          label: "影视学类",
        },
        {
          value: "戏剧学类",
          label: "戏剧学类",
        },
        {
          value: "美术学类",
          label: "美术学类",
        },
        {
          value: "设计学类",
          label: "设计学类",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
      options5: [
        {
          value: "园艺",
          label: "园艺",
        },
        {
          value: "种子科学与工程",
          label: "种子科学与工程",
        },
        {
          value: "设施农业科学与工程",
          label: "设施农业科学与工程",
        },
        {
          value: "农业资源与环境",
          label: "农业资源与环境",
        },
        {
          value: "动物科学",
          label: "动物科学",
        },
        {
          value: "林学",
          label: "林学",
        },
        {
          value: "水产养殖学",
          label: "水产养殖学",
        },
        {
          value: "草业学科",
          label: "草业学科",
        },
        {
          value: "茶学",
          label: "茶学",
        },
        {
          value: "烟草",
          label: "烟草",
        },
        {
          value: "农艺教育",
          label: "农艺教育",
        },
        {
          value: "园艺教育",
          label: "园艺教育",
        },
        {
          value: "其他类别",
          label: "其他类别",
        },
      ],
    };
  },
  methods: {
    searchitem(value) {
      value = value.join("/");
      console.log(value);
      this.article_list = [];
      this.article_list_all.forEach((item) => {
        if (item.article_major.indexOf(value) != -1) {
          this.article_list.push(item);
        }
      });
    },
    handleSelect(key) {
      // console.log(key, keyPath);
      if (key == 1) {
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        this.article_recommond.forEach((item) => {
          this.article_list.push(item);
        });
      }
      if (key == 2) {
        this.article_list = [];
        this.searchchoicenum = 1;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("文学") != -1) {
            this.article_list.push(item);
          }
          // console.log(item);
        });
      }
      if (key == 3) {
        this.article_list = [];
        this.searchchoicenum = 2;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("理学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 4) {
        this.article_list = [];
        this.searchchoicenum = 3;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("工学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 5) {
        this.article_list = [];
        this.searchchoicenum = 4;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("艺术学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 6) {
        this.article_list = [];
        this.searchchoicenum = 5;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("农学") != -1) {
            this.article_list.push(item);
          }
        });
      }
      if (key == 7) {
        this.article_list = [];
        this.searchchoicenum = 0;
        this.searchchoice = [];
        this.article_list_all.forEach((item) => {
          if (item.article_major.indexOf("其他学科") != -1) {
            this.article_list.push(item);
          }
        });
      }
    },
    search(keywords) {
      let index = 0;
      this.article_list.splice(0);
      let list = [];
      this.messageList.forEach((item) => {
        if (item.toLowerCase().indexOf(keywords.toLowerCase()) != -1) {
          // console.log(index);
          list.push(this.article_list_all[index]);
        }
        index++;
      });
      list.forEach((item) => {
        this.article_list.push(item);
      });
      if (keywords) {
        if (list.length == 0) {
          // 提示总共搜索到几篇文章
          this.$notify({
            title: "未找到相关文章",
            type: "error",
          });
        } else {
          // 提示总共搜索到几篇文章
          this.$notify({
            title: "搜索成功！",
            message: `总共搜索到${list.length}篇文章`,
            type: "success",
          });
        }
      }
      if (keywords && list.length != 0) {
        let keywords_list = [];
        let flag = 0;
        if (localStorage.getItem("keywords_list")) {
          JSON.parse(localStorage.getItem("keywords_list")).forEach((item) => {
            if (item.name.toLowerCase() == keywords.toLowerCase()) {
              flag = 1;
              item.value++;
            }
            keywords_list.push(item);
          });
          if (flag == 0) {
            keywords_list.push({
              name: keywords.toLowerCase(),
              value: 1,
            });
          }
          localStorage.setItem("keywords_list", JSON.stringify(keywords_list));
        } else {
          keywords_list.push({
            name: keywords.toLowerCase(),
            value: 1,
          });
          localStorage.setItem("keywords_list", JSON.stringify(keywords_list));
        }
      }
    },
  },
  components: {
    HomeArticleListVue,
    HomeNewsListVue,
    HomeAuthorListVue,
    HomeFoucsListVue,
    article_home_itemVue,
  },
  mounted() {
    getArticleListAPI().then((res) => {
      if (res.data) {
        // console.log("article", res.data);
        res.data.forEach((item) => {
          // console.log(item);
          this.article_list_all.push(item);
          // 将所有的信息转为字符串，以供后续搜索使用
          this.messageList.push(
            item.article_introduce +
              "/" +
              item.article_title +
              "/" +
              item.article_major +
              "/" +
              item.article_labels +
              "/" +
              item.author_name
          );
        });
        this.article_list_all.reverse();
        this.messageList.reverse();
        // console.log(this.article_list_all);
        // console.log(this.messageList.length);
      }
    });
    // 粗略模拟智能推荐算法
    // 返回min和max之间的一个随机数，包括min和max
    function getRandomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min); // +1是保证可以取到上限值
    }
    // 洗牌函数，模拟随机智能推荐
    function shuffle(arr) {
      let _arr = arr.slice();
      for (let i = 0; i < _arr.length; i++) {
        let j = getRandomInt(0, i);
        let t = _arr[i];
        _arr[i] = _arr[j];
        _arr[j] = t;
      }
      return _arr;
    }
    setTimeout(() => {
      if (localStorage.getItem("keywords_list")) {
        let max1,
          max2 = "";
        let keywords_sort = [];
        JSON.parse(localStorage.getItem("keywords_list")).forEach((item) => {
          keywords_sort.push(item);
        });
        keywords_sort.sort((a, b) => {
          return b.value - a.value;
        });
        max1 = keywords_sort[0].name;
        max2 = keywords_sort[1].name;
        console.log("关键词1", max1, "关键词2", max2);
        // console.log("123123123", this.messageList);
        this.messageList.forEach((item, index) => {
          // console.log("213123", item);
          if (item.toLowerCase().indexOf(max1.toLowerCase()) != -1) {
            // console.log("查找index", index);
            this.article_recommond.push(this.article_list_all[index]);
          }
        });
        this.article_recommond = shuffle(this.article_recommond);
        this.messageList.forEach((item, index) => {
          if (item.toLowerCase().indexOf(max1.toLowerCase()) == -1) {
            if (item.toLowerCase().indexOf(max2.toLowerCase()) != -1) {
              // console.log(index);
              this.article_recommond.push(this.article_list_all[index]);
            }
          }
        });
        this.article_recommond = shuffle(this.article_recommond);
        this.messageList.forEach((item, index) => {
          if (item.toLowerCase().indexOf(max1.toLowerCase()) == -1) {
            if (item.toLowerCase().indexOf(max2.toLowerCase()) == -1) {
              // console.log(index);
              this.article_recommond.push(this.article_list_all[index]);
            }
          }
        });
        this.article_list = this.article_recommond;
      } else {
        this.article_list = this.article_list_all;
      }
      // console.log(this.messageList);
      // console.log(this.article_list);
    }, 500);
  },
};
</script>

