import axios from 'axios'

function myAxios(axiosConfig) {
  const service = axios.create({
    // baseURL: "http://127.0.0.1:4523/m1/1462928-0-default",//本地环境
    // baseURL: "https://mock.apifox.cn/m1/2008323-0-default", //云端环境
    // baseURL: "http://120.77.85.52:30001", //正式环境
    baseURL: "http://localhost:8100", //本地服务器环境(json-server)
    timeout: 5000, //5秒内无响应则报错
  });
  return service(axiosConfig);
}

export default myAxios;