<template>
  <div name="app" :class="classes[i]">
    <el-row type="flex" v-if="this.$route.name != 'login'">
      <CommonHeaderVue
    /></el-row>

    <el-row style="margin-top: 25px">
      <router-view></router-view>
    </el-row>
  </div>
</template>

<script>
import CommonHeaderVue from "./components/CommonHeader.vue";
export default {
  name: "app",
  data() {
    return {
      classes: ["app", "login"],
      i: 0,
    };
  },
  components: { CommonHeaderVue },
  watch: {
    $route: {
      handler: function (newV, _) {
        console.log(newV);
        if (newV.name == "login") {
          this.i = 1;
        } else {
          this.i = 0;
        }
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>

<style scoped>
html,
body {
  width: 100%;
  min-width: 1350px;
  max-width: 1350px;
  margin: 0 auto;
  padding: 0;
}
.app {
  width: 100%;
}
.login {
  height: 100%;
  width: 100%;
  background-size: 100% 100%;
  margin: 0;
  background:
	/* 水平条纹 */ -webkit-linear-gradient(
      top,
      transparent 10px,
      #f0f0f0 10px,
      #cdcdcd 12px,
      transparent 12px,
      transparent 69px,
      #f0f0f0 60px
    ),
    /* 垂直条纹 */ -webkit-linear-gradient(left, transparent 10px, #f0f0f0 10px, #cdcdcd
          12px, transparent 12px, transparent 69px, #f0f0f0 60px);
  -webkit-background-size: 41px 41px;
  -moz-background-size: 41px 41px;
  background-size: 41px 41px;
  -webkit-background-size: 20px 20px;
  -moz-background-size: 20px 20px;
  background-size: 20px 20px;
}
</style>
