<template>
  <div name="articleHomeItem" class="articleHomeItem-wrap">
    <el-row type="flex">
      <div class="title" @click="push()">
        <font>{{ article_title }}</font>
      </div>
      <div
        v-if="article_status == '1'"
        class="copied"
        style="margin: 0 0 0 10px"
      >
        <font>转载</font>
      </div>
      <div
        v-if="article_status == '2'"
        class="original"
        style="margin: 0 0 0 10px"
      >
        <font>原创</font>
      </div>
    </el-row>
    <el-row type="flex" style="margin-top: 10px">
      <div class="article_major">
        <font>{{ major }}</font>
      </div>
      <div
        class="article_labels"
        v-for="(item, index) in article_labels"
        :key="index"
      >
        <font>{{ article_labels[index] }}</font>
      </div>
    </el-row>
    <el-row>
      <div @click="push()">
        <p class="article_introduce">{{ article_introduce }}</p>
      </div>
    </el-row>
    <el-row type="flex" justify="space-between" style="width: 100%">
      <el-col :span="0.5" style="display: flex">
        <el-col :span="0.5">
          <div class="author_head">
            <img :src="author_head" alt="" />
          </div>
        </el-col>
        <el-col :span="0.5" style="margin-left: 20px">
          <el-row type="flex">
            <font class="author_name">{{ author_name }}</font>
            <div class="article_num" style="margin-left: 10px">
              <font v-if="article_status == '1'"
                >本站已收藏该作者的{{ article_num }}篇文章</font
              >
              <font v-if="article_status == '2'"
                >该用户已发表{{ article_num }}篇文章</font
              >
            </div>
          </el-row>
        </el-col>
      </el-col>
      <el-col style="margin-top: 25px" :span="0.5" class="action_list">
