<script>
import { getArticleListAPI, getUserlist } from "@/api/homeAPI/articleslist";
export default {
  name: "articleHomeItem",
  data() {
    return {
      id: this.articleList.id,
      article_status: this.articleList.article_status,
      article_link: this.articleList.article_link,
      article_title: this.articleList.article_title,
      article_major: this.articleList.article_major,
      article_labels: this.articleList.article_labels,
      article_introduce: this.articleList.article_introduce,
      author_id: this.articleList.author_id,
      author_head: this.articleList.author_head_photo,
      author_name: this.articleList.author_name,
      article_num: 0,
      like_num: this.articleList.like_num,
      collection_num: this.articleList.collection_num,
      share_num: this.articleList.share_num,
      comment_num: this.articleList.comment_num,
    };
  },
  computed: {
    major() {
      return this.article_major.join("-");
    },
  },
  methods: {
    push() {
      const routeUrl = this.$router.resolve({
        path: "/articleHome/" + this.id,
      });
      window.open(routeUrl.href, "_blank");
    },
  },
  watch: {
    articleList: {
      handler: function (newV, _) {
        this.id = newV.id;
        this.article_status = newV.article_status;
        this.article_link = newV.article_link;
        this.article_title = newV.article_title;
        this.article_major = newV.article_major;
        this.article_labels = newV.article_labels;
        this.article_introduce = newV.article_introduce;
        this.author_head = newV.author_head_photo;
        this.author_name = newV.author_name;
        this.article_num = 0;
        this.like_num = newV.like_num;
        this.collection_num = newV.collection_num;
        this.share_num = newV.share_num;
        this.comment_num = newV.comment_num;
      },
    },
    immediate: true,
    deep: true,
  },
  mounted() {
    // 如果是用户发布的原创文章，则直接在用户列表中获取信息
    if (this.article_status == "2") {
      getUserlist().then((res) => {
        if (res.data) {
          res.data.forEach((item) => {
            if (item.id == this.author_id) {
              this.author_head = item.user_head;
              this.author_name = item.user_name;
            }
          });
        }
      });
    }
    getArticleListAPI().then((res) => {
      if (res.data) {
        if (this.article_status == "2") {
          res.data.forEach((item) => {
            if (item.author_id == this.author_id) {
              this.article_num++;
            }
          });
        } else {
          res.data.forEach((item) => {
            if (item.author_name == this.author_name) {
              this.article_num++;
            }
          });
        }
      }
    });
  },
  props: ["articleList"],
};
</script>

<style scoped>
.articleHomeItem-wrap {
  position: relative;
  width: 975px;
  border-radius: 5px;
  background: #ffffff;
  padding: 9px 14px;
  margin-bottom: 30px;
  transition: all 0.3s;
}
.articleHomeItem-wrap:hover {
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

.title {
  height: 26px;
  font-family: SourceHanSansCN-Bold;
  font-size: 18px;
  font-weight: bold;
  color: #3d3d3d;
  cursor: pointer;
}
.copied {
  width: max-content;
  padding: 0 10px;
  height: 26px;
  border-radius: 13px;
  background: #ff6c6c;
  display: flex;
  justify-content: center;
  align-items: center;
}
.copied font {
  font-family: SourceHanSansCN-Regular;
