<template>
  <div name="HomeAuthorList" class="HomeAuthorList-wrap">
    <el-row>
      <font class="title">用户文章排行榜</font>
    </el-row>
    <el-row>
      <author_range_itemVue
        v-for="(_, index) in user_list.slice(0, 7)"
        :userList="user_list[index]"
        :key="index"
        :range="index + 1"
    /></el-row>
  </div>
</template>

<script>
import { getArticleListAPI, getUserlist } from "@/api/homeAPI/articleslist";
import author_range_itemVue from "../little/author_range_item.vue";
export default {
  name: "HomeAuthorList",
  data() {
    return {
      user_list_all: [],
      user_list: [],
      register_user_list: [],
    };
  },
  components: { author_range_itemVue },
  mounted() {
    // 首先获取所有的登录用户列表，以备查询
    getUserlist().then((res) => {
      if (res.data) {
        res.data.forEach((item) => {
          this.register_user_list.push(item);
        });
      }
    });
    getArticleListAPI().then((res) => {
      if (res.data) {
        res.data.forEach((item) => {
          if (item.article_status == "1") {
            this.user_list_all.push({
              author_head_photo: item.author_head_photo,
              author_name: item.author_name,
              author_major: "",
              author_status: 1,
              article_num: 0,
            });
          } else {
            this.register_user_list.forEach((item1) => {
              if (item.author_id == item1.id) {
                this.user_list_all.push({
                  author_head_photo: item1.user_head,
                  author_name: item1.user_name,
                  author_major: item1.user_major,
                  author_status: 2,
                  article_num: 0,
                });
              }
            });
          }
        });
        // 去重
        let result = [];
        let obj = {};
        for (let i = 0; i < this.user_list_all.length; i++) {
          if (!obj[this.user_list_all[i].author_name]) {
            result.push(this.user_list_all[i]);
            obj[this.user_list_all[i].author_name] = true;
          }
        }
        result.forEach((item) => {
          this.user_list.push(item);
        });
        this.user_list.forEach((item1) => {
          this.user_list_all.forEach((item2) => {
            if (item1.author_name == item2.author_name) {
              item1.article_num++;
            }
          });
        });
        // 排序
        this.user_list.sort((a, b) => {
          return b.article_num - a.article_num;
        });
      }
    });
  },
};
</script>

<style scoped>
.HomeAuthorList-wrap {
  margin-top: 20px;
  padding: 10px;
  position: relative;
  width: 300px;
  border-radius: 5px;
  opacity: 1;
  background: #ffffff;
}
.title {
  height: 35px;
  font-family: SourceHanSansCN-Bold;
  font-size: 24px;
  font-weight: bold;
  color: #3d3d3d;
}
.tips {
  height: 14px;
  font-family: SourceHanSansCN-Regular;
  font-size: 10px;
  font-weight: normal;
  color: #3d3d3d;
  opacity: 0.5;
}
</style>