<template>
  <div name="newHomeItem" class="newHomeItem-wrap">
    <el-row type="flex">
      <el-col :span="0.5">
        <div class="author_head">
          <img :src="author_head" alt="" />
        </div>
      </el-col>
      <el-col :span="0.5" style="margin: 0 10px 0 10px">
        <div class="article_title" @click="push()">
          <font>{{ article_title }}</font>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex">
      <el-col :span="0.5">
        <el-row style="margin-top: 5px">
          <font class="author_name">{{ author_name }}</font>
        </el-row>
        <el-row
          style="margin-top: 5px; flex-flow: row wrap"
          type="flex"
          class="article_labels"
        >
          <font>标签：</font>
          <div v-for="(item, index) in article_labels" :key="index">
            <font>{{ article_labels[index] }}</font>
          </div>
          <font v-if="article_labels.length == 0">暂无相关标签</font>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getUserlist } from "@/api/homeAPI/articleslist";
export default {
  name: "newHomeItem",
  data() {
    return {
      id: this.newsList.id,
      article_status: this.newsList.article_status,
      author_id: this.newsList.author_id,
      author_name: this.newsList.author_name,
      author_head: this.newsList.author_head_photo,
      article_title: this.newsList.article_title,
      article_labels: this.newsList.article_labels,
    };
  },
  methods: {
    push() {
      const routeUrl = this.$router.resolve({
        path: "/articleHome/" + this.id,
      });
      window.open(routeUrl.href, "_blank");
    },
  },
  mounted() {
    // 如果是用户发布的原创文章，则直接在用户列表中获取信息
    if (this.article_status == "2") {
      getUserlist().then((res) => {
        if (res.data) {
          res.data.forEach((item) => {
            if (item.id == this.author_id) {
              this.author_head = item.user_head;
              this.author_name = item.user_name;
            }
          });
        }
      });
    }
  },
  props: ["newsList"],
};
</script>

<style scoped>
.newHomeItem-wrap {
  border: 0.5px solid #00ead8;
  border-radius: 10px;
  margin: 10px 0;
  padding: 10px;
  position: relative;
  width: 280px;
}
.author_head {
  width: 36px;
  height: 36px;
  background-color: #76fff5;
  border-radius: 18px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.author_head img {
  width: 36px;
}
.article_title font {
  font-family: SourceHanSansCN-Medium;
  font-size: 16px;
  font-weight: 500;
  color: #3d3d3d;
  cursor: pointer;
}
.article_title:hover font {
  color: #00ead8;
}
.author_name {
  height: 17px;
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  font-weight: normal;
  color: #3d3d3d;
}
.article_labels > div {
  width: max-content;
  margin: 0 5px;
  padding: 0 10px;
  height: 20px;
  border-radius: 10px;
  background: #d9fe32;
  display: flex;
  justify-content: center;
  align-items: center;
}
.article_labels > font {
  font-family: SourceHanSansCN-Regular;
  font-size: 12px;
  font-weight: normal;
  color: #3d3d3d;
}
</style>