import myAxios from '../axios';

//获取主文章列表
export function getArticleListAPI(paramsList) {
  return myAxios({
    url: '/articlelist',
    method: 'get',
    params: paramsList
  })
}

// 上传文章，添加到本地数据
export function postArticleListAPI(paramsList) {
  return myAxios({
    url: '/articlelist',
    method: 'post',
    data: paramsList
  })
}

// 获取用户总列表
export function getUserlist(paramsList) {
  return myAxios({
    url: '/userlist',
    method: 'get',
    params: paramsList
  })
}

// 注册时增加新用户
export function registerUser(paramsList) {
  return myAxios({
    url: '/userlist',
    method: 'post',
    data: paramsList
  })
}

// 获取相关用户的关键搜索词信息
export function getKeywordslist(paramsList) {
  return myAxios({
    url: '/keywordslist',
    method: 'get',
    params: paramsList
  })
}

// 提交、修改用户信息
export function changeUserInfo(paramsList) {
  return myAxios({
    url: `/userlist/${paramsList.user_id}`,
    method: 'patch',
    data: paramsList
  })
}