import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home/index.vue'

Vue.use(VueRouter)

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [{
  path: '/',
  redirect: '/login',
},
{
  path: '/login',
  name: 'login',
  component: () => import('../views/login/index.vue'),
},
{
  path: '/home',
  name: 'home',
  component: home,
},
{
  path: '/articleHome/:id',
  name: 'articleHome',
  component: () => import('../views/articleHome/index.vue'),
},
{
  path: '/postArticle',
  name: 'postArticle',
  component: () => import('../views/post/index.vue'),
},
{
  path: '/personalCenter',
  name: 'personalCenter',
  redirect: "/MyMain",
  component: () => import('../views/personalCenter/index.vue'),
  children: [{
    path: '/MyMain',
    name: 'MyMain',
    component: () => import('../components/ModelPersonalCenter/MyMain/Main.vue'),
  }, {
    path: '/MyArticles',
    name: 'MyArticles',
    component: () => import('../components/ModelPersonalCenter/MyArticle/ArticlePost.vue'),
  }, {
    path: '/MyCollection',
    name: 'MyCollection',
    component: () => import('../components/ModelPersonalCenter/MyCollection/ArticleCollection.vue'),
  }, {
    path: '/MyInfo',
    name: 'MyInfo',
    component: () => import('../components/ModelPersonalCenter/MyInfo/InfoMain.vue'),
  }, {
    path: '/MyFocus',
    name: 'MyFocus',
    redirect: '/MyFocusList',
    component: () => import('../components/ModelPersonalCenter/MyFocus/index.vue'),
    children: [{
      path: '/MyFocusList',
      name: 'MyFocusList',
      component: () => import('../components/ModelPersonalCenter/MyFocus/MyFocusList.vue'),
    }, {
      path: '/MyFansList',
      name: 'MyFansList',
      component: () => import('../components/ModelPersonalCenter/MyFocus/MyFansList.vue'),
    }]
  }]
},

]

export default new VueRouter({
  mode: 'history',
  routes
})