<template>
  <div name="HomeNewsList" class="HomeNewsList-wrap">
    <el-row>
      <font class="title">最新发布</font>
    </el-row>
    <el-row>
      <el-col :span="0.5">
        <new_home_itemVue
          v-for="(_, index) in news_list"
          :newsList="news_list[index]"
          :key="index"
        />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { getArticleListAPI } from "@/api/homeAPI/articleslist";
import new_home_itemVue from "../little/new_home_item.vue";
export default {
  name: "HomeNewsList",
  data() {
    return {
      news_all_list: [],
      news_list: [],
    };
  },
  mounted() {
    getArticleListAPI().then((res) => {
      if (res.data) {
        res.data.forEach((item) => {
          this.news_all_list.push(item);
        });
      }
    });
    setTimeout(() => {
      this.news_list = this.news_all_list.reverse().slice(0, 5);
    }, 500);
  },
  components: { new_home_itemVue },
};
</script>

<style scoped>
.HomeNewsList-wrap {
  margin-top: 20px;
  padding: 10px;
  width: 300px;
  border-radius: 5px;
  background: #ffffff;
}
.title {
  height: 35px;
  font-family: SourceHanSansCN-Bold;
  font-size: 24px;
  font-weight: bold;
  color: #3d3d3d;
}
</style>